export default {

    data() {      
        return {
            url:"",
			deviceId:"",
			ipv4:"",
			ipText:"未连接盒子WIFI",
			btnText:"ID",
			// topBar:false,
			topBar1:false
			
        }
    },
	components: {
	        dragButton
	    },
    methods : {
		longClick:function(){
			// let that=this,times;
			// clearTimeout(times);
			// that.topBar=!that.topBar;
			// that.btnText="切换";
			// if(!that.topBar){
			// 	that.btnText="长按设置";
			// 	times=setTimeout(function(){
			// 		that.btnText="长按设置";
			// 	},10000);
			// }
		},
		btnClick:function(){
			let that=this,times;
			that.topBar1=!that.topBar1; 
			times=setTimeout(function(){
				that.topBar1=false;
			},10000);
			
		},
		register:function(){
			uni.navigateTo({ url:"./toGetiIP?id=1" });
		},
		Switch:function(){
			uni.navigateTo({ url:"./toGetiIP?id=2" });
		},
        /*
         * goControl,goSwitch,goHistory,goList 页面跳转函数
         * navigateTo微信提供的跳转函数，跳转后可以返回之前的页面
         */
        goControl: function() {
            // const url='../config/setDeviceId?ipv4='+this.ipv4;
            // uni.navigateTo({ url:url });
			const url='../config/config';
			uni.navigateTo({ url:url });
			

        },
        goSwitch: function() {
			if(this.deviceId==""){
				uni.showToast({
				    title:"当前设备ID为空，请先配置设备ID",
				    icon: 'loading',
				    duration: 1000
				   })
				return
			}
            // const url='../switching/repeatPWD';
			const url='../switching/SwitchAccount';
            uni.navigateTo({ url:url });
        },
        goHistory: function() {
			if(this.deviceId==""){
				uni.showToast({
				    title:"当前设备ID为空，请先配置设备ID",
				    icon: 'loading',
				    duration: 1000
				   })
				return
			}
            const url='../historyReport/history';
			
            uni.navigateTo({ url:url });
        },
        goList : function () {
			if(this.deviceId==""){
				uni.showToast({
				    title:"当前设备ID为空，请先配置设备ID",
				    icon: 'loading',
				    duration: 1000
				   })
				return
			}
            const url='../setting/ruleSetting';
            uni.navigateTo({url:url })
        },

